<template>
  <van-icon
    color="#777"
    :name="attitude===-1?'good-job-o':'good-job'"
    :class="{
              isLike:attitude===1
            }"
    @click="like"

  />
</template>

<script>
import {addLike, unLike} from "../api/article/like";

export default {
  name: "Like",
  components: {},
  props: ['articleId', 'attitude'],
  data() {
    return {loading: false,}
  },
  methods: {
    async like() {
      this.$toast.loading('请等待');
      if (this.attitude == -1) {
        //? 如果是无态度
        try {
          await addLike(this.articleId);
          this.$toast.success('喜欢成功');
          this.$emit('update:attitude', 1);
        } catch (e) {
          this.$toast.fail('喜欢失败');
        }

      } else {
        try {
          await unLike(this.articleId);
          this.$toast.success('已取消喜欢');
          this.$emit('update:attitude', -1);
        } catch (e) {
          this.$toast.fail('取消喜欢失败');
        }
      }

    }
  },
  computed: {},
  watch: {},
  created() {
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.isLike {
  color: #e74c3c !important;
}
</style>
