<template>
  <van-button
    v-if="isFollow"
    round
    size="small"
    :loading="loading"
    @click="unFollowHandle"
  >已关注
  </van-button>
  <van-button
    v-else
    type="info"
    color="#3296fa"
    round
    size="small"
    icon="plus"
    :loading="loading"
    @click="addFollowHandle"
  >关注
  </van-button>

</template>

<script>
import {unFollow, addFollow} from "../api/article/follow";

export default {
  name: "Follow",
  components: {},
  props: {
    isFollow: Boolean,
    autId: [Number, String]
  },
  data() {
    return {
      loading: false,
    }
  },
  methods: {
    async unFollowHandle() {
      this.loading = true;
      try {
        let {data} = await unFollow(this.autId)
        this.$emit('update:isFollow', false);
        this.$toast.success("取关成功");
        // console.log(data);
      } catch (e) {
        this.$toast.fail("取关失败")
      }
      this.loading=false
    },
    async addFollowHandle() {
      this.loading = true;
      try {
        let {data} = await addFollow(this.autId)
        this.$emit('update:isFollow', true);
        this.$toast.success("关注成功");
        // console.log(data);
      } catch (e) {
        this.$toast.fail("关注失败")
      }
      this.loading = false;
    }
  },
  computed: {},
  watch: {},
  created() {
  },
  mounted() {
  }
}
</script>

