<template>
  <van-icon
    color="#777"
    :name="!isCollection?'star-o':'star'"
    :class="{
              isLike:isCollection
            }"
    @click="like"

  />
</template>

<script>
import {addCollect, unCollect} from "../api/article/collection";

export default {
  name: "Collection",
  components: {},
  props: ['articleId', 'isCollection'],
  data() {
    return {loading: false,}
  },
  methods: {
    async like() {
      this.$toast.loading('请等待');
      if (!this.isCollection) {
        //? 如果是无态度
        try {
          await addCollect(this.articleId);
          this.$toast.success('收藏成功');
          this.$emit('update:isCollection', true);
        } catch (e) {
          this.$toast.fail('收藏失败');
        }

      } else {
        try {
          await unCollect(this.articleId);
          this.$toast.success('已取消收藏');
          this.$emit('update:isCollection', false);
        } catch (e) {
          this.$toast.fail('取消收藏失败');
        }
      }

    }
  },
  computed: {},
  watch: {},
  created() {
  },
  mounted() {


    /**
     * evaporate
     * antagonism
     * antagonism
     * foul
     * metabolism
     * trivial
     *
     * */
  }
}
</script>

<style scoped lang="less">
.isLike {
  color:#f39c12 !important;
}
</style>
